define('spaircorp-ui/routes/admin/scripts/edit/process/edit', ['exports', 'ember'], function (exports, _ember) {
  exports['default'] = _ember['default'].Route.extend({

    notify: _ember['default'].inject.service(),

    model: function model(params) {
      // FIXME work out this later
      this.transitionTo('admin.scripts.edit', this.modelFor('admin.scripts.edit'));
    },

    afterModel: function afterModel(model) {
      _ember['default'].assert('Must pass a valid node', model && model.id && model.obj && model.obj.data && model.obj.data.type);
      _ember['default'].assert('Must pass process node', model.obj.data.type === 'process');

      // If no controls object has yet been generated - create it
      if (!_ember['default'].isArray(model.obj.data.controls)) {
        model.obj.data.controls = [];
      }

      // Inject the appropriate branching control
      var decisionConnection = model.obj.getSourceEdges().find(function (item) {
        return item.target && item.target.getType() === 'decision';
      });
      if (decisionConnection) {
        (function () {
          // Remove existing one
          var existingItem = model.obj.data.controls.find(function (item) {
            return item.type === 'decision';
          });
          debugger;
          model.obj.data.controls.removeObject(existingItem);

          // Add new one
          var options = [];
          decisionConnection.target.getSourceEdges().forEach(function (item) {
            options.push({
              label: item.data.label,
              targetLabel: item.target.data.text
            });
          });
          // targetType: item.target.data.type
          _ember['default'].set(decisionConnection.target.data, 'options', options);

          model.obj.data.controls.push(decisionConnection.target.data);
        })();
      }

      debugger;
    },

    actions: {

      close: function close() {
        this.transitionTo('admin.scripts.edit', this.modelFor('admin.scripts.edit'));
      }

    }
  });
});
